	.Level .level-item {
		display: flex;
		flex-direction: column;
	}

	.Level h1,
	h5 {
		margin-bottom: 0;
		text-align: center;
	}

	.Level h1 {
		font-weight: 600;
		font-size: 3em;
	}

	.Level h4 {
		margin-bottom: -0.5rem;
	}

	.Level h5 {
		font-weight: 600;
		font-size: 1.2em;
		text-align: center;
		margin-bottom: 25px;
	}

	.Level .is-cherry h1, h5 { color: red; }

	.Level .is-cherry h4 {color: red;}

	.Level .is-blue h1, h5 { color: yellow; }

	.Level .is-blue h4 {color: yellow;}

	.Level .is-green h1, h5 { color: green; }

	.Level .is-green h4 {color: green;}

	.Level .is-gray h1, h5 { color: gray; }

	.Level .is-gray h4 {color: gray;} 

	#footer-bottom {
