	}

	.footer-bottom-flex {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	#footer-bottom #copyright {
		margin-right: auto;
	}

	#footer-bottom #copyright a {
		text-decoration:none;
		color: #999;
	}
